        color: #099d8d;
        font-weight: 500;
        line-height: 45px;}
        
    .team  {
        max-width: 50%;
        margin: 0 auto;
        display:inline-block;
        display:flex;
        flex-direction: column;}

/*------------------------FORTALEZAS Y DEBILIDADES---------------------------*/

    .second-section {
        padding-left: 138px;
        padding-right: 138px;
        padding-bottom: 60px;
        display: flex;}

    .second.strengths-block {
        width: 50%;
        padding-left: 15px;
        padding-right: 15px;
        margin-right: 45px;}

    .second.weaknesses-block {
        width: 50%;
        padding-left: 15px;
        padding-right: 15px;}

    .strengths {
    margin-right: 19.1px;}

    .weaknesses {
        width: 439px;}

    .strengths_p {
        width: 439px;}

    .weaknesses_p{
        width: 439px; }

/*-------------------------------INTEGRANTES-----------------------------*/

    .persons {
        width: 79%;
        margin-left: auto;
        margin-right: auto;}
    
    .photo{
        width: 268px;
        height: 268px;}

    .presentation.left {
        padding-right: 138px;}

    .presentation.right {
        padding-left: 138px;}

/*----------------------------------FOOTER--------------------------------*/
    .logo {
        padding-bottom: 51px;
        margin-left: 138px;}

    .footer-navigation {
        width: 50%;
        margin-right: 138px;
        padding-top: 30px;
        line-height: 2.14;
        order: 2;
        text-align: end;}

    .copyright {
        width: 20%;
        margin-left: 138px;
        padding-top: 30px; 
        order: 1;
        text-align: start;}

}

